/**
 * @description parse elem html
 *
 */
import $, { Dom7Array } from 'dom7';
import { Editor, Element, Descendant, Text } from 'slate';
import { IDomEditor } from '../editor/interface';
import parseElemHtml from './parse-elem-html';
import {
  PARSE_ELEM_HTML_CONF,
  ParseElemHtmlFnType,
  PARSE_STYLE_HTML_FN_LIST,
} from './index';
import { NodeType, DOMElement } from '../utils/dom';
import {
  replaceSpace160,
  tryInsertTextToChildrenLastItem,
  checkChildNodeChildren,
} from './helper';

/**
 *  slate node children
 * @param $elem $elem
 * @param editor editor
 */
function genChildren($elem: Dom7Array, editor: IDomEditor): Descendant[] {
  const children: Descendant[] = [];

  // void node html  video  html  data-k-e-is-void   children
  const isVoid = $elem.attr('data-k-e-is-void') != null;
  if (isVoid) {
    return children;
  }

  const childNodes = $elem[0].childNodes;

  //  child  <br>
  if (childNodes.length === 1) {
    if (childNodes[0].nodeName === 'BR') {
      children.push({ text: '' });
      return children; // 
    }
  }

  //  DOM  slate elem node children
  childNodes.forEach((child) => {
    if (child.nodeType === NodeType.ELEMENT_NODE) {
      // <br>  children  text  `\n`
      if (child.nodeName === 'BR') {
        //  text  children
        const res = tryInsertTextToChildrenLastItem(children, '\n');
        if (!res) {
          //  item
          children.push({ text: '\n' });
        }
        return;
      }

      //  elem
      const $child = $(child);
      const childNode = parseElemHtml($child, editor) as any;
      let childNodeChildren = childNode.textChildren || [];
      if (childNodeChildren.length && Text.isText(childNode)) {
        // console.log(checkChildNodeChildren(childNodeChildren), 'parseHtmlchildNodeChildren1')
        const isEmptyText = checkChildNodeChildren(childNodeChildren);
        childNodeChildren = childNodeChildren.map((item) => {
          return { ...item, isEmptyText };
        });
        children.push(...childNodeChildren);
      } else {
        children.push(childNode);
      }
      return;
    }
    if (child.nodeType === NodeType.TEXT_NODE) {
      // text
      let text = child.textContent || '';
      if (text.trim() === '' && text.indexOf('\n') >= 0) {
        // 
        return;
      }

      if (text) {
        //  charCode 160 `&nbsp`  charCode 32 JS 
        text = replaceSpace160(text);

        //  text  children
        const res = tryInsertTextToChildrenLastItem(children, text);
        if (!res) {
          //  item
          children.push({ text });
        }
      }
      return;
    }
  });
  return children;
}

/**
 *  parseElemHtml  paragraph
 * @param elem elem
 * @param children children
 */
function defaultParser(
  elem: DOMElement,
  children: Descendant[],
  editor: IDomEditor,
): Element {
  return {
    type: 'paragraph',
    children: [{ text: $(elem).text().replace(/\s+/gm, ' ') }],
  };
}

/**
 *  html  parseElemHtml 
 * @param $elem $elem
 */
function getParser($elem: Dom7Array): ParseElemHtmlFnType {
  for (let selector in PARSE_ELEM_HTML_CONF) {
    if ($elem[0].matches(selector)) {
      return PARSE_ELEM_HTML_CONF[selector];
    }
  }
  return defaultParser;
}

/**
 *  DOM elem html  span font  elem
 * @param $elem $elem
 * @param editor editor
 * @returns slate element
 */
function parseCommonElemHtml($elem: Dom7Array, editor: IDomEditor): Element {
  const children = genChildren($elem, editor);

  // parse
  const parser = getParser($elem);
  let elem = parser($elem[0], children, editor);

  const isVoid = Editor.isVoid(editor, elem);
  if (!isVoid) {
    //  void  children 
    if (children.length === 0) {
      elem.children = [{ text: $elem.text().replace(/\s+/gm, ' ') }];
    }

    //  style
    PARSE_STYLE_HTML_FN_LIST.forEach((fn) => {
      elem = fn($elem[0], elem, editor) as Element;
    });
  }

  return elem;
}

export default parseCommonElemHtml;
