import { DISPLAY_FLEX_GRID, PRE_PARSE_HTML_CONF_LIST } from './index';
/**
 * @description parse node html
 *
 */

import $, { Dom7Array } from 'dom7';
import { Descendant } from 'slate';
import { IDomEditor } from '../editor/interface';
import parseCommonElemHtml from './parse-common-elem-html';
import parseTextElemHtml from './parse-text-elem-html';
import { getStyles, getTagName } from '../utils/dom';
import { TEXT_TAGS } from '../index';

/**
 *  DOM Elem html
 * @param $elem $elem
 * @param editor editor
 * @returns slate Descendant
 */
function parseElemHtml($elem: Dom7Array, editor: IDomEditor): Descendant {
  // pre-parse
  PRE_PARSE_HTML_CONF_LIST.forEach((conf) => {
    const { selector, preParseHtml } = conf;
    if ($elem[0].matches(selector)) {
      $elem = $(preParseHtml($elem[0]));
    }
  });

  const tagName = getTagName($elem);
  const parent = $elem.parent();

  // <span>  data-k-e-type  elem  text
  if (tagName === 'span') {
    const { display } = getStyles($elem);
    const { display: parentDisplay } = getStyles(parent);
    /**
     * displayinlineelemflexgridelem
     * span display (!!display && !['inline', 'inline !important'].includes(display))
     */
    if (DISPLAY_FLEX_GRID.includes(parentDisplay)) {
      $elem.attr('data-k-e-type', 'block');
      return parseCommonElemHtml($elem, editor);
    } else if ($elem.attr('data-k-e-type')) {
      return parseCommonElemHtml($elem, editor);
    } else {
      return parseTextElemHtml($elem, editor);
    }
  }

  // <code> 
  if (tagName === 'code') {
    const parentTagName = getTagName(parent);
    if (parentTagName === 'pre') {
      // <code>  <pre>  elem
      return parseCommonElemHtml($elem, editor);
    } else {
      // <code>  <pre>  text
      return parseTextElemHtml($elem, editor);
    }
  }

  //  <code> 
  if (TEXT_TAGS.includes(tagName)) {
    // text node
    return parseTextElemHtml($elem, editor);
  } else {
    // elem node
    return parseCommonElemHtml($elem, editor);
  }
}

export default parseElemHtml;
