/**
 * @description  node 
 *
 */

import { Element, Editor, Node, Ancestor } from 'slate';
import { IDomEditor } from '../editor/interface';
import { NODE_TO_INDEX, NODE_TO_PARENT } from '../utils/weak-maps';

/**
 * createEditor  selector  node  NODE_TO_PARENT, NODE_TO_INDEX  
 * @param node node
 * @param index index
 * @param parent parent node
 * @param editor editor
 */
function bindNodeRelation(
  node: Node,
  index: number,
  parent: Ancestor,
  editor: IDomEditor,
) {
  //  weakMap 
  NODE_TO_INDEX.set(node, index);
  NODE_TO_PARENT.set(node, parent);

  if (Element.isElement(node)) {
    const { children = [] } = node;
    children.forEach((child: Node, i: number) =>
      bindNodeRelation(child, i, node, editor),
    ); // 

    const isVoid = Editor.isVoid(editor, node);
    if (isVoid) {
      const [[text]] = Node.texts(node);
      //  text  weakMap
      NODE_TO_INDEX.set(text, 0);
      NODE_TO_PARENT.set(text, node);
    }
  }
}

export default bindNodeRelation;
