/**
 * @description parse text html
 *
 */

import $, { Dom7Array } from 'dom7';
import { Descendant, Text } from 'slate';
import { IDomEditor } from '../editor/interface';
import { PARSE_STYLE_HTML_FN_LIST } from './index';
import { deReplaceHtmlSpecialSymbols } from '../utils/util';
import { replaceSpace160, tryInsertTextToChildrenLastItem } from './helper';
import { NodeType } from '../utils/dom';
import parseElemHtml from './parse-elem-html';

/**
 *  slate text node children
 * @param $text $text
 * @param editor editor
 */
function genTextChildren(
  editor: IDomEditor,
  $text: Dom7Array,
  parentNode: any,
): Descendant[] {
  const textChildren: Descendant[] = [];

  // children
  if ($text[0].childNodes.length === 0) return textChildren;

  // void node html  video  html  data-k-e-is-void   children
  const isVoid = $text.attr('data-k-e-is-void') != null;
  if (isVoid) {
    return textChildren;
  }

  const childNodes = $text[0].childNodes;

  //  child  <br>
  if (childNodes.length === 1) {
    if (childNodes[0].nodeName === 'BR') {
      textChildren.push({ text: '\n' });
      return textChildren;
    }
  }

  const { style } = parentNode;

  const parentStyle = style ? { ...style } : undefined;

  //  DOM  slate elem node children
  childNodes.forEach((child) => {
    if (child.nodeType === NodeType.ELEMENT_NODE) {
      // <br>  children  text  `\n`
      if (child.nodeName === 'BR') {
        //  text  children
        const res = tryInsertTextToChildrenLastItem(textChildren, '\n');
        if (!res) {
          //  item
          textChildren.push({ text: '\n', style: parentStyle });
        }
        return;
      }

      //  elem
      const $child = $(child);
      const childNode = parseElemHtml($child, editor) as any;
      const childNodeChildren = childNode.textChildren || [];
      if (childNodeChildren.length && Text.isText(childNode)) {
        const newChildren = childNodeChildren.map((item) => {
          if (parentStyle) {
            item.style = { ...parentStyle, ...(item.style || {}) };
          }
          if (child.parentNode?.nodeName === 'STRONG') {
            item.bold = true;
          }
          if (child.parentNode?.nodeName === 'EM') {
            item.italic = true;
          }
          return item;
        });
        textChildren.push(...newChildren);
      } else {
        if (parentStyle) {
          childNode.style = { ...parentStyle, ...(childNode.style || {}) };
        }
        if (child.parentNode?.nodeName === 'STRONG') {
          childNode.bold = true;
        }
        if (child.parentNode?.nodeName === 'EM') {
          childNode.italic = true;
        }
        textChildren.push(childNode);
      }
      return;
    }
    if (child.nodeType === NodeType.TEXT_NODE) {
      // text
      let text = child.textContent || '';
      if (text.trim() === '' && text.indexOf('\n') >= 0) {
        // 
        return;
      }

      if (text) {
        //  charCode 160 `&nbsp`  charCode 32 JS 
        text = replaceSpace160(text);

        //  text  children
        const res = tryInsertTextToChildrenLastItem(textChildren, text);
        if (!res) {
          //  item
          const textItem: any = { text, style: parentStyle };
          if (child.parentNode?.nodeName === 'STRONG') {
            textItem.bold = true;
          }
          if (child.parentNode?.nodeName === 'EM') {
            textItem.italic = true;
          }
          textChildren.push(textItem);
        }
      }
      return;
    }
  });
  return textChildren;
}

/**
 *  text elem  <span> <strong> <em>  DOM Text Node
 * @param $text $text
 * @param editor editor
 * @returns slate text
 */
function parseTextElemHtml($text: Dom7Array, editor: IDomEditor): Text {
  // if ($text.parents('pre').length === 0) {
  //   //  <pre> 
  //   // 1.  2.  <br>  `\n`
  //   $text[0].innerHTML = $text[0].innerHTML
  //     .replace(/\s+/gm, ' ')
  //     .replace(/<br>/g, '\n');
  // }

  //  textContent  .text()  text  &nbsp;
  let text = $text[0].textContent || '';

  // html  &lt;  <
  text = deReplaceHtmlSpecialSymbols(text);

  //  charCode 160 `&nbsp`  charCode 32 JS 
  text = replaceSpace160(text);

  //  text node
  let textNode = { text } as any;

  //  style
  PARSE_STYLE_HTML_FN_LIST.forEach((fn) => {
    textNode = fn($text[0], textNode, editor) as Text;
  });

  /**
   * text node
   */
  if (Text.isText(textNode)) {
    const textChildren = genTextChildren(editor, $text, textNode);
    if (textChildren.length) {
      // @ts-ignore
      textNode.textChildren = textChildren;
    }
  }
  return textNode;
}

export default parseTextElemHtml;
